import React from "react";

import { MenuItem } from '../menu-item/menu-item.component'

import './directory.styles.scss'

class Directory extends React.Component {
    constructor() {
        super();

        this.state = {
            sections: [
                {
                  title: 'rolodex',
                  imageUrl: 'https://ibb.co/0Fp8PfR',
                  linkUrl: 'rolodex',
                  id: 1
                },
                {
                  title: 'app2',
                  imageUrl: 'https://ibb.co/7YH7F37',
                  linkUrl: 'app2',
                  id: 2
                },
                {
                  title: 'app3',
                  imageUrl: 'https://ibb.co/ftdrj8x',
                  linkUrl: 'app3',
                  id: 3
                },
                {
                  title: 'app4',
                  imageUrl: 'https://ibb.co/tBXWg9S',
                  linkUrl: 'app4',
                  size: 'large',
                  id: 4
                },
                {
                  title: 'app5',
                  imageUrl: 'https://ibb.co/09nGpMp',
                  linkUrl: 'app4',
                  size: 'large',
                  id: 5
                }
            ]
        };
    }
    render() {
        return(
            <div className='directory-menu'>
            {this.state.sections.map(({id, ...otherSectionProps}) => (
                <MenuItem key={id} {...otherSectionProps}/>
            ))}
            </div>
        );
    }
}

export default Directory;